import { Navigate, Outlet } from "react-router-dom"
import { ApplicationEntitlement } from "@rp/common/api-types"
import { useAccount } from "@rp/react/common/providers"

type Props = {
  requiredEntitlements: Array<ApplicationEntitlement>
  blockedPage?: string
}
const EntitlementRoute = ({ requiredEntitlements, blockedPage = "/" }: Props) => {
  const { hasEntitlement } = useAccount()
  const userHasRequiredEntitlements = requiredEntitlements.every(entitlement =>
    hasEntitlement(entitlement),
  )

  return userHasRequiredEntitlements ? <Outlet /> : <Navigate to={blockedPage} replace />
}

export { EntitlementRoute }
